.header {
   /* display: flex;
    background-color: rebeccapurple;
    width: 100%;
    box-shadow: 2px 2px 5px blue;*/

    display: flex;
    /*background-color: #000;*/
    flex: 0 0 auto;
    width: 100%;
    box-shadow: 1px 2px 6px blue;
    /*height: 60px;*/
    background: rgb(0,0,36);
    background: linear-gradient(90deg, rgba(0,0,36,1) 0%, rgba(9,9,121,1) 47%, rgb(1, 34, 56) 100%);
    color: #fff;
    animation: mymove 1.2s infinite;
}
@keyframes mymove {
    50% {box-shadow: 2px 4px 5px blue;}
}

.logoContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 300px;
    flex-grow: 1;
    min-width:80px;
    margin: 10px;
}

.logo {
    width: 50px;
    height: 50px;
    border-radius: 30px;
    -webkit-animation:spin 14s linear infinite;
    -moz-animation:spin 14s linear infinite;
    animation:spin 14s linear infinite;
    }
    @-moz-keyframes spin {
        100%{ -moz-transform: rotate(360deg); }
    }
    @-webkit-keyframes spin {
        100% { -webkit-transform: rotate(360deg); }
    }
    @keyframes spin {
    100% {
        -webkit-transform: rotate(360deg);
        transform:rotate(360deg);
    }
    /*width: 15vw;*/
    /*margin: 5px 10px;*/
}

.login {
    color: white;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 15px;
    /*opacity: 0.7;*/
    font-size: 18px;
}

.login a {
    color: white;
}

.button {
    /*background-color: #000; !* Green *!*/
    border: none;
    color: lightgrey;
    padding: 5px 3px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    box-shadow: 1px 3px 3px rgb(8, 8, 199);
    cursor: pointer;
    padding: 8px;

transition-duration: 0.1s;
}
