.menu {
    display: flex;
    flex-wrap: wrap;
    margin: 0 10px;
    justify-content: space-around;
    align-items: center;
    width: 70vw;
    font-size: 18px;
}

.menu div {
    /*margin: 0 20px;*/
}

.linkContainer {
    /*border: blue 1px solid;*/
    border-radius: 5px;
    box-shadow: 0 2px 2px #1d1dfc;
    display: inline-flex;
    padding: 6px;
    margin: 2px 2px 5px;
}

.linkContainer:hover {
    box-shadow: 1px 5px 5px blue;
    transform: scale(1.05);
    cursor: pointer;
}

.linkContainer:active {
    box-shadow: 1px 5px 5px blue;
    transform: scale(0.95);
}

.menu div a {
    text-decoration: none;
    opacity: 0.7;
    color: #fff;
}

