.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
    align-items: center;
    max-width: 1200px;
    min-width: 300px;

}

.postsCont {
    margin: 10px 0;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    max-width: 700px;
    /*min-width: 800px;*/
    background: #f9f7f7;
    padding: 20px 10px;
    border-radius: 15px;
    box-shadow: 2px 1px 2px 2px lightgray;
    /*width: 500px;*/
    width: 70vw;
}


.posts {
    display: flex;
    flex-direction: column;
}

