.item {
    margin: 25px;
    padding: 10px 20px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5); /* Параметры тени */
    border-radius: 10px;
    display: flex;
    overflow: hidden;
}

.logo {
    width: 60px;
    height: 60px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5); /* Параметры тени */
    border-radius: 5px;
}

.likeImg {
    width: 30px;
    height: 30px;
    margin-top: 10px;
}

.likeImg:hover {
    transform: scale(1.1);
    /*box-shadow: 0 0 10px rgba(0,0,0,0.5); !* Параметры тени *!*/
}

.likeImg:active {
    transform: scale(0.9);
    transition-duration: 0.5s;
}

.logodiv {
    display: flex;
    margin: 10px;
}

.textCont{
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.txt {
    display: flex;
    /*flex-direction: column;*/
    margin: 10px;
    align-items: flex-start;
}

.formCont {
    display: flex;
    flex-direction: column;
    align-items: end;
}

.buttonCont {
    margin: 3px 10px;
}

.textarea div div textarea {
    font-size: 16px;
    font-size: max(16px, 1em);
    font-family: inherit;
    padding: 0.25em 0.5em;
    background-color: #fff;
    /*border: 2px solid black;*/
    border-radius: 4px;
    resize: vertical;
    min-height: 120px;
    outline: none;
    -moz-appearance: none;
    border: 1px solid #999; /* указание этого свойства также удалит обводку в FireFox */
    width: 45vw;
    max-width: 500px;
}




.addPostButtonCont {
    cursor: pointer;
    height: 40px;
    margin: -5px 6px;
}

.addPostButton:hover {
    transform: scale(1.1);
    /*transition: 0.3s;*/
    box-shadow: 1px 1px 1px blue;
}

.addPostButtonCont:active {
    transform: scale(0.98);
    transition: 0.3s;
}

.addPostButton {
    height: 30px;
    box-shadow: 1px 1px 1px blue;

}

