.container {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    overflow: auto;
    flex-direction: column;
}

.card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 15px;
    box-shadow: blue 2px 2px 6px 1px;
    width: 320px;
    /*height: 300px;*/
    padding: 20px;
    font-family: "Comic Sans MS";
}

.cardTitle {
    /*font-family: "Comic Sans MS";*/
    font-size: 1.5em;
}

.inputs input {
    padding: 2px 4px;
    height: 25px;
    width: 260px;
    border-radius: 5px;
    font-size: 20px;
    font-family: Roboto;
}

.rememberMe {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    font-size: 20px;
    margin-top: 15px;
    font-family: Roboto;
    /*font-family: "Comic Sans MS";*/
}

.captchaImg {
    padding: 2px;
    border-radius: 10px;
    margin-top: 20px;
    box-shadow: 0px 1px 3px 2px red;
}

.buttonContainer {
    margin: 15px 0;
    display: flex;
    justify-content: center;
    font-weight: 600;
}

.button {

    /*background-color: #000; !* Green *!*/
    /*border: 1px solid black;*/
    /*color: white;*/
    padding: 5px 3px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    box-shadow: 1px 3px 3px rgb(8, 8, 199);
    cursor: pointer;
    padding: 8px;

    transition-duration: 0.1s;
}

.button:hover {
    /*border: none;*/
    box-shadow: 0px 4px 5px rgb(8, 8, 238);
}

.button:active {
    border: none;
    opacity: 0.9;
    transform: scale(0.95);
    box-shadow: 3px 6px 7px rgb(2, 2, 30), -3px 5px 5px rgb(4, 4, 94);
    font-weight: 600;
}