/*   Кнопка загрузки изображения */

.ProfileInfo_inputButton__1kGZY {
    position: relative;
    display: inline-block;
    /*transition: opacity .3s;*/
}
.ProfileInfo_inputButton__1kGZY span {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 14px;
    vertical-align: middle;
    color: rgb(255 255 255);
    text-align: center;
    border-radius: 4px;
    background-color: #0808ee;
    box-shadow: #0808ee 1px 1px 1px 1px;
    line-height: 22px;
    height: 30px;
    padding: 4px 10px;
    box-sizing: border-box;
    border: none;
    margin: 0;
    transition: background-color 0.2s;

}
.ProfileInfo_inputButton__1kGZY input[type=file] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    display: block;
    width: 0;
    height: 0;
}

/* Focus */
.ProfileInfo_inputButton__1kGZY input[type=file]:focus + span {
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
}

/* Hover/active */
.ProfileInfo_inputButton__1kGZY:hover span {
    background-color: #1d1dfc;
}
.ProfileInfo_inputButton__1kGZY:active span {
    background-color: #04045e;
}

/* Disabled */
.ProfileInfo_inputButton__1kGZY input[type=file]:disabled + span {
    background-color: #eee;
}
