
.user_item {
    display: flex;
    flex-wrap: wrap;
}

.left {
    display: flex;
    flex-direction: column;
    margin: 15px;
    padding: 10px;
    justify-content: center;
    align-items: center;
}


.ava {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    width: 95px;
    height: 90px;
    border-radius: 4px;
    margin-bottom: 5px;
}

.button {
    border-radius: 10px;
    height: 30px;
    width: 150px;
    background: #c7fcec;
    margin-top: 20px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 500;
}


.right {
    display: flex;
    justify-content: space-between;
    margin: 15px;
    padding: 10px;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0.2, 0.5);
    width: 70vw;
    font-size: 18px;
    overflow: hidden;
    max-width: 500px;
}

.name {
    display: flex;
    flex-direction: column;
    margin: 15px;
    /*justify-content: space-between;*/
}

.status {
    padding: 2px;
    margin: 2px 4px;
    font-size: 14px;
}


.loc {
    display: flex;
    flex-direction: column;
    margin: 15px;
}

.pages {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
}

.pages span {
    margin: 1px;
    padding: 5px;
}

.pages span:hover {
    cursor: pointer;
    transform: scale(1.5);

}

.selectedPage {
    font-weight: 700;
}

.loadCont {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 0;
}

.load {
    position: relative;
    z-index: 999;
    top: 135px;
    /*margin: 50px;*/
    /*width: 200px;*/
    /*height: 200px;*/
}