.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 50px;
    font-size: 14px;
    /*background-color: #000;*/
    width: 100%;
    box-shadow: 1px 2px 6px 3px #04045e;
    color: #fff;
    flex: 0 0 auto;
    background: rgb(0,0,36);
    background: linear-gradient(90deg, rgba(0,0,36,1) 0%, rgba(9,9,121,1) 47%, rgb(1, 34, 56) 100%);
}
